<template>
  <div class="container">
    <div class="box">
      <el-form :model="article" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm" :label-position="position" >
        <el-form-item label="" prop="title">
          <el-input v-model="article.title" :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tag">
          <el-radio v-model="article.tag" label="0" :disabled="disabled"></el-radio>
          <el-radio v-model="article.tag" label="1" :disabled="disabled"></el-radio>
        </el-form-item>
        <el-form-item label="" prop="intro">
          <el-input type="textarea" placeholder="" v-model="article.intro"
                    class="text" resize="none" :autosize="{ minRows: 3, maxRows: 10 }">
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            :action="serverUrl"
            ref="upload"
            :class="{disabled:uploadDisabled}"
            list-type="picture-card"
            :limit="1"
            accept="image/*"
            :data="qiniuData"
            :on-change="handleChange"
            :on-remove="handleRemove"
            :on-success="uploadAvatarSuccess"
            :on-error="uploadAvatarError"
            :before-upload="beforeAvatarUpload"
          >
          <i class="el-icon-plus"></i>
          <div slot="tip" class="el-upload__tip">100kb</div>
          </el-upload>
        </el-form-item>
<!--        <el-form-item label="">-->
<!--          <el-upload-->
<!--            :action="serverUrl"-->
<!--            :data="qiniuData"-->
<!--            multiple-->
<!--            :on-success="uploadAvatarSuccess"-->
<!--            :on-error="uploadAvatarError"-->
<!--          >-->
<!--            <el-button size="small" type="primary"></el-button>-->
<!--            <div slot="tip" class="el-upload__tip"></div>-->
<!--          </el-upload>-->
<!--        </el-form-item>-->
        <el-form-item label="" prop="content">
          <quill-editor
            class="editor"
            v-model="article.content"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
          </quill-editor>
          <el-upload
            class="avatar-uploader"
            :action="serverUrl"
            accept="image/*"
            :data="qiniuData"
            :show-file-list="false"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :before-upload="beforeAvatarUpload"
          ></el-upload>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="submitForm">{{btnName}}</el-button>
    </div>
  </div>
</template>

<script>
const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'], //    
  [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
  // [{ size: ['10px', '12px', '14px', '16px', '18px', '20px', '26px', '32px'] }], // 
  // [{ font: ['SimSun', 'SimHei', 'Microsoft-YaHei', 'KaiTi', 'FangSong', 'Arial', 'Times-New-Roman', 'sans-serif'] }], // 
  [{ color: [] }, { background: [] }], // 
  [{ list: 'ordered' }, { list: 'bullet' }], // 
  [{ script: 'sub' }, { script: 'super' }], // /
  [{ indent: '-1' }, { indent: '+1' }], // 
  [{ align: [] }], // 
  ['clean'], // 
  ['link', 'image'] // 
]
export default {
  props: {
    value: {
      type: String
    },
    maxSize: {
      type: Number,
      default: 4000
    }
  },
  data() {
    return {
      disabled: false,
      position: '',
      btnName: '',
      article: {
        title: '',
        intro: '',
        content: '',
        coverUrl: '',
        adminId: [],
        tag: '0'
      },
      qiniuData: {
        key: '',
        token: ''
      },
      avatarUrl: '',
      uploadDisabled: false,
      content: this.value,
      quillUpdateImg: false, // loadingfalse,
      serverUrl: 'https://upload.qiniup.com', // 
      qiniuaddr: 'http://q7wdge0nf.bkt.clouddn.com/',
      header: {
      // token: sessionStorage.token
      }, // token
      editorOption: {
        theme: 'snow', // or 'bubble'
        placeholder: '',
        modules: {
          toolbar: {
            container: toolbarOptions,
            handlers: {
              image: function (value) {
                if (value) {
                  // input
                  document.querySelector('.avatar-uploader input').click()
                } else {
                  this.quill.format('image', false)
                }
              }
              // link: function(value) {
              //   if (value) {
              //     var href = prompt('url');
              //     this.quill.format("link", href);
              //   } else {
              //     this.quill.format("link", false);
              //   }
              // },
            }
          },
          imageDrop: true,
          imageResize: {
            displayStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            modules: ['Resize', 'DisplaySize', 'Toolbar']
          }
        }
      },
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tag: [
          { required: true }
        ]
      }
    }
  },
  created () {
    this.setLabelPosition()
    this.article.adminId = this.$store.getters.adminId
  },
  mounted() {
    this.getQiniuToken()
    if(this.$store.state.article.id !== '') {
      this.disabled = true
      this.btnName = ''
      this.article.title = this.$store.state.article.title
      this.article.intro = this.$store.state.article.intro
      this.article.content = this.$store.state.article.content
      this.article.coverUrl = this.$store.state.article.coverUrl
    } else this.disabled = false
  },
  computed: {
    listData() {
      return this.$store.state.article
    }
  },
  methods: {
    setLabelPosition() {
      if(document.body.clientWidth < 450) {
        this.position = 'top'
      } else this.position = 'left'
    },
    guid() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0
        var v = c === 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
      })
    },
    async getQiniuToken() {
      const { data: res } = await this.$http.get('oss/token', { params: { bucket: 'ooscn' } })
      if(res.status === 605) {
        this.qiniuData.token = res.data
      }
    },
    handleChange(file, fileList) {
      this.uploadDisabled = fileList.length >= 1
    },
    handleRemove(file, fileList) {
      this.article.coverUrl = ''
      this.uploadDisabled = fileList.length >= 1
    },
    beforeAvatarUpload: function(file) {
      this.qiniuData.key = this.guid() + file.name.substring(file.name.lastIndexOf('.'))
      this.quillUpdateImg = true
    },
    uploadAvatarSuccess(res, file, fileList) {
      this.article.coverUrl = this.qiniuaddr + res.key
      this.$message.success('')
    },
    // eslint-disable-next-line handle-callback-err
    uploadAvatarError(err, file, fileList) {
      this.$message.error('')
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    removeDomain (item) {
      var index = this.ruleForm.descs.indexOf(item)
      if (index !== -1) {
        this.ruleForm.descs.splice(index, 1)
      }
    },
    addDomain () {
      this.ruleForm.descs.push({
        value: '',
        key: Date.now()
      })
    },
    onEditorBlur () {
      // 
    },
    onEditorFocus () {
      // 
    },
    onEditorChange () {
      // 
      this.$emit('input', this.content)
    },
    uploadSuccess (res, file) {
      var quill = this.$refs.myQuillEditor.quill
      if (res.hash !== null && res.key !== null) {
        var length = quill.getSelection().index
        quill.insertEmbed(length, 'image', this.qiniuaddr + res.key)
        quill.setSelection(length + 1)
      } else {
        this.$message.error('')
      }
      this.quillUpdateImg = false
    },
    uploadError () {
      this.quillUpdateImg = false
      this.$message.error('')
    },
    submitForm () {
      this.$refs.ruleForm.validate(async valid => {
        if(!valid) return this.$message.error('')
        if(this.article.coverUrl === '') {
          if(this.article.tag === 1) this.article.coverUrl = 'http://q7wdge0nf.bkt.clouddn.com/defaultNoticeCover.jpg'
          else this.article.coverUrl = 'http://q7wdge0nf.bkt.clouddn.com/defaultNewsCover.jpg'
        }
        if(this.$store.state.article.id === '') {
          const { data: res } = await this.$http.post('admin/article', this.$qs.stringify(this.article))
          if(res.status === 600) {
            this.$message.success('')
            this.$refs.ruleForm.resetFields()
          }else this.$message.error('')
        } else{
          var params = {
            articleId: this.$store.state.article.id,
            intro: this.article.intro,
            content: this.article.content,
            coverUrl: this.article.coverUrl
          }
          const { data: res } = await this.$http.post('admin/article/update', this.$qs.stringify(params))
          if(res.status === 600) {
            this.$message.success('')
            this.$store.commit('changeComponentName', 'first')
          }else this.$message.error('')
        }
      })
    }
  }
}
</script>
