<script>
export default {
  data() {
    return {
      teacherDialogVisible: false,
      studentDialogVisible: false,
      checkList: [],
      schoolList: [],
      students: [],
      coacheList: [],
      searchStr: '',
      s_schoolList: []
    }
  },
  created () {
    this.getSchoolInfoList()
  },
  methods: {
    async getSchoolInfoList() {
      const { data: res } = await this.$http.get('admin/schools')
      if(res.status === 605) {
        this.schoolList = res.data
        this.s_schoolList = res.data
      }
    },
    async getCoachInfor() {
      if(this.checkList.length === 0) {
        this.$message.error('')
      } else {
        const { data: res } = await this.$http.get('admin/coachBySchool', { params: { schoolIds: this.checkList + '' } })
        if (res.status === 605) {
          this.coacheList = res.data
          this.teacherDialogVisible = true
        }
      }
    },
    async getStudentInfor() {
      if(this.checkList.length === 0) {
        this.$message.error('')
      } else {
        const { data: res } = await this.$http.get('admin/studentBySchool', { params: { schoolIds: this.checkList + '' } })
        if (res.status === 605) {
          this.students = res.data
          this.studentDialogVisible = true
        }
      }
    },
    async agreeApplication() {
      if(this.checkList.length === 0) {
        this.$message.error('')
      } else {
        const { data: res } = await this.$http.post('admin/passSchool', this.$qs.stringify({ schoolIds: this.checkList + '' }))
        if (res.status === 600) {
          this.$message.success('')
          this.getSchoolInfoList()
        } else this.$message.error('')
      }
    },
    async refuseApplication() {
      if(this.checkList.length === 0) {
        this.$message.error('')
      } else {
        const { data: res } = await this.$http.post('admin/rejectSchool', this.$qs.stringify({ schoolIds: this.checkList + '' }))
        if (res.status === 600) {
          this.$message.success('')
          this.getSchoolInfoList()
        } else this.$message.error('')
      }
    },
    search () {
      this.clearTimer()
      this.timer = setTimeout(() => {
        if(this.searchStr !== '') {
          this.s_schoolList = []
          for(var i in this.schoolList) {
            if(JSON.stringify(this.schoolList[i]).search(this.searchStr) >= 0) {
              this.s_schoolList.push(this.schoolList[i])
            }
          }
        } else this.s_schoolList = this.schoolList
        return this.s_schoolList
      }, 500)
    },
    clearTimer() {
      if (this.timer) {
        clearTimeout(this.timer)
      }
    }
  }
}
</script>

